        sales: 61
      },
      {
        type: '4月',
        sales: 145
      },
      {
        type: '5月',
        sales: 82
      },
      {
        type: '6月',
        sales: 38
      },
      {
        type: '7月',
        sales: 66
      },
      {
        type: '8月',
        sales: 55
      },
      {
        type: '9月',
        sales: 148
      },
      {
        type: '10月',
        sales: 109
      },
      {
        type: '11月',
        sales: 138
      },
      {
        type: '12月',
        sales: 38
      }
    ]

    const columnPlot = new Column(chartEl, {
      height: 300,
      forceFit: true,
      data,
      padding: 'auto',
      xField: 'type',
      yField: 'sales',
      meta: {
        type: {
          alias: '类别'
        },
        sales: {
