<template>
  <div ref="chart"></div>
</template>

<script>
import { Ring } from '@antv/g2plot'
export default {
  mounted () {
    const { $refs: { chart: chartEl } } = this
    const data = [
      {
        type: '分类一',
        value: 27
      },
      {
        type: '分类二',
        value: 25
      },
      {
        type: '分类三',
        value: 18
      },
      {
        type: '分类四',
        value: 15
      },
      {
        type: '分类五',
        value: 10
      },
      {
        type: '其它',
        value: 5
      }
    ]

    const ringPlot = new Ring(chartEl, {
      forceFit: true,
      height: 400,
      radius: 0.8,
      padding: 'auto',
      data,
      angleField: 'value',
      colorField: 'type'
    })
    ringPlot.render()
  }
}
</script>

